import React, { useState } from 'react';
import { MapPin, Phone, Mail, Clock, Send, MessageCircle, Users, Award } from 'lucide-react';

const Contact: React.FC = () => {
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    phone: '',
    company: '',
    subject: '',
    message: '',
    contactType: 'consultation'
  });

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    // 
    console.log('Form submitted:', formData);
    alert('');
  };

  const contactInfo = [
    {
      icon: <MapPin className="w-6 h-6 text-green-600" />,
      title: "",
      content: "99",
      detail: " 611130"
    },
    {
      icon: <Phone className="w-6 h-6 text-blue-600" />,
      title: "",
      content: "028-66772000",
      detail: " 9:00-17:30"
    },
    {
      icon: <Mail className="w-6 h-6 text-orange-600" />,
      title: "",
      content: "agri-center@cdnu.edu.cn",
      detail: "24"
    },
    {
      icon: <Clock className="w-6 h-6 text-purple-600" />,
      title: "",
      content: " 9:00-17:30",
      detail: " 9:00-12:00"
    }
  ];

  const departments = [
    {
      name: "",
      phone: "028-66772000  1001",
      email: "tech@cdnu.edu.cn",
      description: "",
      color: "bg-green-100 text-green-700"
    },
    {
      name: "",
      phone: "028-66772000  1002",
      email: "training@cdnu.edu.cn",
      description: "",
      color: "bg-blue-100 text-blue-700"
    },
    {
      name: "",
      phone: "028-66772000  1003",
      email: "partner@cdnu.edu.cn",
      description: "",
      color: "bg-orange-100 text-orange-700"
    },
    {
      name: "",
      phone: "028-66772000  1004",
      email: "office@cdnu.edu.cn",
      description: "",
      color: "bg-purple-100 text-purple-700"
    }
  ];

  const faqItems = [
    {
      question: "",
      answer: "3"
    },
    {
      question: "",
      answer: ""
    },
    {
      question: "",
      answer: ""
    },
    {
      question: "",
      answer: ""
    }
  ];

  return (
    <div className="min-h-screen bg-gray-50">
      <div className="container mx-auto px-4 py-8">
        {/*  */}
        <div className="text-center mb-12">
          <h1 className="text-4xl font-bold text-gray-800 mb-4"></h1>
          <p className="text-lg text-gray-600 max-w-2xl mx-auto">
            
          </p>
        </div>

        <div className="grid lg:grid-cols-3 gap-8">
          {/*  */}
          <div className="lg:col-span-2">
            <div className="bg-white rounded-lg shadow-md p-8">
              <h2 className="text-2xl font-semibold text-gray-800 mb-6"></h2>
              <form onSubmit={handleSubmit} className="space-y-6">
                <div className="grid md:grid-cols-2 gap-6">
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">
                       *
                    </label>
                    <input
                      type="text"
                      name="name"
                      value={formData.name}
                      onChange={handleInputChange}
                      required
                      className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent"
                      placeholder=""
                    />
                  </div>
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">
                       *
                    </label>
                    <input
                      type="email"
                      name="email"
                      value={formData.email}
                      onChange={handleInputChange}
                      required
                      className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent"
                      placeholder=""
                    />
                  </div>
                </div>
                
                <div className="grid md:grid-cols-2 gap-6">
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">
                      
                    </label>
                    <input
                      type="tel"
                      name="phone"
                      value={formData.phone}
                      onChange={handleInputChange}
                      className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent"
                      placeholder=""
                    />
                  </div>
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">
                      /
                    </label>
                    <input
                      type="text"
                      name="company"
                      value={formData.company}
                      onChange={handleInputChange}
                      className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent"
                      placeholder=""
                    />
                  </div>
                </div>

                <div className="grid md:grid-cols-2 gap-6">
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">
                       *
                    </label>
                    <select
                      name="contactType"
                      value={formData.contactType}
                      onChange={handleInputChange}
                      required
                      className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent"
                    >
                      <option value="consultation"></option>
                      <option value="training"></option>
                      <option value="cooperation"></option>
                      <option value="support"></option>
                      <option value="other"></option>
                    </select>
                  </div>
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">
                       *
                    </label>
                    <input
                      type="text"
                      name="subject"
                      value={formData.subject}
                      onChange={handleInputChange}
                      required
                      className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent"
                      placeholder=""
                    />
                  </div>
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                     *
                  </label>
                  <textarea
                    name="message"
                    value={formData.message}
                    onChange={handleInputChange}
                    required
                    rows={6}
                    className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent"
                    placeholder="..."
                  />
                </div>

                <button
                  type="submit"
                  className="w-full flex items-center justify-center px-6 py-3 bg-green-600 text-white rounded-lg hover:bg-green-700 transition-colors font-medium"
                >
                  <Send className="w-5 h-5 mr-2" />
                  
                </button>
              </form>
            </div>

            {/*  */}
            <div className="bg-white rounded-lg shadow-md p-8 mt-8">
              <h2 className="text-2xl font-semibold text-gray-800 mb-6"></h2>
              <div className="bg-gray-200 rounded-lg h-64 flex items-center justify-center">
                <div className="text-center text-gray-600">
                  <MapPin className="w-12 h-12 mx-auto mb-2" />
                  <p>...</p>
                  <p className="text-sm mt-2">99 </p>
                </div>
              </div>
            </div>
          </div>

          {/*  */}
          <div className="lg:col-span-1">
            {/*  */}
            <div className="bg-white rounded-lg shadow-md p-6 mb-6">
              <h3 className="text-lg font-semibold text-gray-800 mb-4"></h3>
              <div className="space-y-4">
                {contactInfo.map((info, index) => (
                  <div key={index} className="flex items-start space-x-3">
                    <div className="flex-shrink-0">
                      {info.icon}
                    </div>
                    <div>
                      <h4 className="font-medium text-gray-800">{info.title}</h4>
                      <p className="text-gray-600">{info.content}</p>
                      <p className="text-sm text-gray-500">{info.detail}</p>
                    </div>
                  </div>
                ))}
              </div>
            </div>

            {/*  */}
            <div className="bg-white rounded-lg shadow-md p-6 mb-6">
              <h3 className="text-lg font-semibold text-gray-800 mb-4"></h3>
              <div className="space-y-4">
                {departments.map((dept, index) => (
                  <div key={index} className="border border-gray-200 rounded-lg p-4">
                    <div className="flex items-center justify-between mb-2">
                      <h4 className="font-medium text-gray-800">{dept.name}</h4>
                      <span className={`px-2 py-1 rounded-full text-xs ${dept.color}`}>
                        
                      </span>
                    </div>
                    <p className="text-sm text-gray-600 mb-2">{dept.description}</p>
                    <div className="text-xs text-gray-500 space-y-1">
                      <div className="flex items-center">
                        <Phone className="w-3 h-3 mr-1" />
                        {dept.phone}
                      </div>
                      <div className="flex items-center">
                        <Mail className="w-3 h-3 mr-1" />
                        {dept.email}
                      </div>
                    </div>
                  </div>
                ))}
              </div>
            </div>

            {/*  */}
            <div className="bg-gradient-to-br from-green-500 to-green-600 rounded-lg shadow-md p-6 text-white mb-6">
              <h3 className="text-lg font-semibold mb-4 flex items-center">
                <Award className="w-5 h-5 mr-2" />
                
              </h3>
              <div className="space-y-3 text-sm">
                <div className="flex items-center">
                  <MessageCircle className="w-4 h-4 mr-2 flex-shrink-0" />
                  <span>24</span>
                </div>
                <div className="flex items-center">
                  <Users className="w-4 h-4 mr-2 flex-shrink-0" />
                  <span></span>
                </div>
                <div className="flex items-center">
                  <Award className="w-4 h-4 mr-2 flex-shrink-0" />
                  <span>100%</span>
                </div>
              </div>
            </div>

            {/*  */}
            <div className="bg-white rounded-lg shadow-md p-6">
              <h3 className="text-lg font-semibold text-gray-800 mb-4"></h3>
              <p className="text-sm text-gray-600 mb-4">
                
              </p>
              <button className="w-full px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors flex items-center justify-center">
                <MessageCircle className="w-4 h-4 mr-2" />
                
              </button>
            </div>
          </div>
        </div>

        {/*  */}
        <section className="mt-16">
          <h2 className="text-3xl font-semibold text-gray-800 mb-8 text-center"></h2>
          <div className="grid md:grid-cols-2 gap-6">
            {faqItems.map((item, index) => (
              <div key={index} className="bg-white rounded-lg shadow-md p-6">
                <h3 className="text-lg font-semibold text-gray-800 mb-3">{item.question}</h3>
                <p className="text-gray-600 leading-relaxed">{item.answer}</p>
              </div>
            ))}
          </div>
        </section>
      </div>
    </div>
  );
};

export default Contact;