import React from 'react';
import { Cpu, Database, Smartphone, BarChart3 } from 'lucide-react';

const Services: React.FC = () => {
  const solutions = [
    {
      icon: <Cpu className="w-12 h-12 text-green-600" />,
      title: "",
      description: "",
      features: ["", "", "", ""]
    },
    {
      icon: <Database className="w-12 h-12 text-blue-600" />,
      title: "",
      description: "",
      features: ["", "", "", ""]
    },
    {
      icon: <Smartphone className="w-12 h-12 text-orange-600" />,
      title: "",
      description: "",
      features: ["", "", "", ""]
    },
    {
      icon: <BarChart3 className="w-12 h-12 text-purple-600" />,
      title: "",
      description: "",
      features: ["", "", "", ""]
    }
  ];

  const cases = [
    {
      title: "",
      client: "XX",
      scale: "10000",
      result: "15%20%",
      description: ""
    },
    {
      title: "",
      client: "XX",
      scale: "5000",
      result: "30%",
      description: ""
    },
    {
      title: "",
      client: "XX",
      scale: "200",
      result: "100%",
      description: ""
    }
  ];

  return (
    <div className="min-h-screen bg-gray-50">
      <div className="container mx-auto px-4 py-8">
        {/*  */}
        <div className="text-center mb-12">
          <h1 className="text-4xl font-bold text-gray-800 mb-4"></h1>
          <p className="text-lg text-gray-600 max-w-2xl mx-auto">
            
          </p>
        </div>

        {/*  */}
        <section className="mb-16">
          <h2 className="text-3xl font-semibold text-gray-800 mb-8 text-center"></h2>
          <div className="grid md:grid-cols-2 gap-8">
            {solutions.map((solution, index) => (
              <div key={index} className="bg-white rounded-lg shadow-lg p-6 hover:shadow-xl transition-shadow">
                <div className="flex items-start space-x-4">
                  <div className="flex-shrink-0">
                    {solution.icon}
                  </div>
                  <div className="flex-1">
                    <h3 className="text-xl font-semibold text-gray-800 mb-3">{solution.title}</h3>
                    <p className="text-gray-600 mb-4">{solution.description}</p>
                    <div className="flex flex-wrap gap-2">
                      {solution.features.map((feature, idx) => (
                        <span key={idx} className="px-3 py-1 bg-green-100 text-green-700 text-sm rounded-full">
                          {feature}
                        </span>
                      ))}
                    </div>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </section>

        {/*  */}
        <section>
          <h2 className="text-3xl font-semibold text-gray-800 mb-8 text-center"></h2>
          <div className="space-y-6">
            {cases.map((caseItem, index) => (
              <div key={index} className="bg-white rounded-lg shadow-md p-6">
                <div className="flex flex-col lg:flex-row lg:items-center lg:justify-between mb-4">
                  <div>
                    <h3 className="text-xl font-semibold text-gray-800 mb-2">{caseItem.title}</h3>
                    <div className="flex flex-wrap gap-4 text-sm text-gray-600">
                      <span>{caseItem.client}</span>
                      <span>{caseItem.scale}</span>
                    </div>
                  </div>
                  <div className="mt-4 lg:mt-0">
                    <span className="inline-block px-4 py-2 bg-green-100 text-green-700 rounded-full text-sm font-medium">
                      {caseItem.result}
                    </span>
                  </div>
                </div>
                <p className="text-gray-600 leading-relaxed">{caseItem.description}</p>
              </div>
            ))}
          </div>
        </section>

        {/*  */}
        <section className="mt-16 bg-white rounded-lg shadow-md p-8">
          <h2 className="text-3xl font-semibold text-gray-800 mb-8 text-center"></h2>
          <div className="grid md:grid-cols-4 gap-6">
            <div className="text-center">
              <div className="w-16 h-16 bg-green-100 rounded-full flex items-center justify-center mx-auto mb-4">
                <span className="text-green-600 font-bold text-xl">1</span>
              </div>
              <h3 className="font-semibold text-gray-800 mb-2"></h3>
              <p className="text-sm text-gray-600"></p>
            </div>
            <div className="text-center">
              <div className="w-16 h-16 bg-blue-100 rounded-full flex items-center justify-center mx-auto mb-4">
                <span className="text-blue-600 font-bold text-xl">2</span>
              </div>
              <h3 className="font-semibold text-gray-800 mb-2"></h3>
              <p className="text-sm text-gray-600"></p>
            </div>
            <div className="text-center">
              <div className="w-16 h-16 bg-orange-100 rounded-full flex items-center justify-center mx-auto mb-4">
                <span className="text-orange-600 font-bold text-xl">3</span>
              </div>
              <h3 className="font-semibold text-gray-800 mb-2"></h3>
              <p className="text-sm text-gray-600"></p>
            </div>
            <div className="text-center">
              <div className="w-16 h-16 bg-purple-100 rounded-full flex items-center justify-center mx-auto mb-4">
                <span className="text-purple-600 font-bold text-xl">4</span>
              </div>
              <h3 className="font-semibold text-gray-800 mb-2"></h3>
              <p className="text-sm text-gray-600"></p>
            </div>
          </div>
        </section>
      </div>
    </div>
  );
};

export default Services;